<template>
  <BaseModal
    title="Manage Sources"
    @close="setManageSourcesModalShown(false)"
  >
    <div class="w-full lg:w-1/3 bg-gray-900 p-4 m-auto rounded">
      <h1>Manage Sources</h1>
      <Container
        class="h-full"
        group-name="board"
        @drop="onDrop"
        drag-class="ghost"
      >
        <Draggable v-for="source in sources" :key="source.code">
          {{ source }}
        </Draggable>
      </Container>
    </div>
  </BaseModal>
</template>

<script>
import { playerListGetters, playerListActions, interfaceActions } from '@store/helpers'
import { Container, Draggable } from 'vue-smooth-dnd'
import applyDrag from '@utils/dnd/applyDrag'

export default {
  data() {
    return {
      dropPlaceholderOptions: {
        className: 'drop-preview',
        animationDuration: '150',
        showOnTop: true
      }
    }
  },
  components: {
    Draggable,
    Container
  },
  computed: {
    ...playerListGetters,
  },
  methods: {
    ...playerListActions,
    ...interfaceActions,
    onDrop(dropResults) {
      this.updateSources(applyDrag(this.sources, dropResults))
    }
  }
}
</script>

