import sources from '@assets/sources.json'

export const state = {
  input: {
    text: '',
  },
  result: null,
  sources: [
    ...sources,
  ],
}

export const getters = {
  sources: state => state.sources,
}

export const actions = {
  updateInput({ commit, state }, inputChanges) {
    const newInput = {
      ...state.input,
      ...inputChanges
    }
    commit('SET_INPUT', newInput)
  },
  addSource({ commit }, newValue) {
    commit('ADD_SOURCE', newValue)
    
    commit('SET_ADD_SOURCE_MODAL_SHOWN', false)
    commit('SET_MENU_MODAL_SHOWN', false)
  },
  updateSources({ commit }, newValue) {
    commit('SET_SOURCES', newValue)
  },
}

export const mutations = {
  SET_INPUT(state, newValue) {
    state.input = newValue
  },
  ADD_SOURCE(state, newValue) {
    state.sources.push(newValue)
  },
  SET_SOURCES(state, newValue) {
    state.sources = newValue
  },
}
