import { mapState, mapGetters, mapActions } from 'vuex'

export const interfaceGetters = {
  ...mapState({
    gridColNum: state => state.interface.gridColNum,
    gridRowNum: state => state.interface.gridRowNum,
    menuModalShown: state => state.interface.menuModalShown,
    addSourceModalShown: state => state.interface.addSourceModalShown,
    manageSourcesModalShown: state => state.interface.manageSourcesModalShown,
    settingsModalShown: state => state.interface.settingsModalShown,
  }),
  ...mapGetters([
    'interfaceVariables',
  ])
}

export const interfaceActions = mapActions([
  'setMenuModalShown',
  'setAddSourceModalShown',
  'setManageSourcesModalShown',
  'setSettingsModalShown',
  'setGridRowNum',
  'setGridColNum',
])

export const playerListGetters = {
  ...mapState({
    input: state => state.playerList.input,
  }),
  ...mapGetters([
    'sources',
  ])
}

export const playerListActions = mapActions([
  'updateInput',
  'addSource',
  'updateSources',
])