<template>
  <div
    ref="suggestionsContainer"
    class="relative flex w-full"
  >
    <div 
      :class="$style.searchContainer" 
      class="relative flex flex-grow"
    >
      <BaseInputText      
        ref="searchInput"
        :value="input.text"
        class="bg-transparent flex flex-grow z-20" 
        placeholder="Add source"
        v-on="$listeners"
        v-bind="$attrs"
        @keydown.enter="confirm()"
        @input="updateInput({ text: $event })"
        @focus="activate"
        @blur="deactivate"
      >
        <slot slot="prefix"/>
      </BaseInputText>

      <div
        class="absolute w-full p-3 text-grey-text bg-gray-900 border z-10 border-primary border-t-0 rounded shadow-md rounded-t-none -mt-1"
        v-show="isOpen"
        :class="$style.previewContainer"
        @mousedown.prevent
        @click.prevent
      >
        <p
          class="py-1 px-0 m-0 mb-2 text-base font-normal"
        >
          What are you searching for
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { playerListGetters, playerListActions } from '@store/helpers'

export default {
  data() {
    return {
      isActive: false,
      currentStepIndex: 0,
    }
  },
  computed: {
    ...playerListGetters,
    isOpen() {
      return (
        this.isActive && this.input.text.trim()
      )
    }
  },
  destroyed() {
    this.updateInput({ text: '' })
  },
  methods: {
    ...playerListActions,
    async focusSearchInput() {
      this.$nextTick(() => {
        const { input } = this.$refs.searchInput.$refs
        input.focus()
        input.select()
      })
    },
    activate({ step = 0, focus = false }) {
      if (this.isActive) return
      this.isActive = true
      this.currentStepIndex = step
      if (focus) this.focusSearchInput()
    },
    deactivate() {
      this.isActive = false
    },
    confirm() {
      this.addSource({ type: 'youtube', code: this.input.text })
    }
  }
}
</script>

<style lang="scss" module>
.searchButton {

}

.searchContainer {
  input:focus {
    background: none;
  }
}

.previewContainer {
  top: 100%;
}
</style>