<template>
  <div>
    <div class="flex flex-no-shrink flex-grow mb-0">
      <div
        :class="focusedClasses"
        class="appearance-none flex w-full bg-grey-darker text-grey-text border rounded"
      >
        <slot name="prefix"/>
        <input 
          ref="input"
          type="text" 
          v-bind="$attrs"
          :class="$style.input"
          class="appearance-none block font-mono w-full bg-grey-darker text-grey-text py-3 px-4 rounded"
          v-on="listeners"
        >
        <slot name="postfix"/>
      </div>
    </div>
  </div>
</template>

<script>
import cuid from 'cuid'

export default {
  inheritAttrs: false,
  props: {
    value: {
      type: [String, Number],
      default: ''
    },
    type: {
      type: String,
      default: 'text'
    },
    id: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      inputId: this.id ? this.id : cuid(),
      isFocused: false,
      inputValue: this.value
    }
  },
  computed: {
    focusedClasses() {
      if (this.isFocused) {
        return [
          'border-primary',
          'bg-grey-lighter',
          'shadow',
          this.$style.focused
        ]
      } else {
        return ['border-grey-darkest']
      }
    },
    listeners() {
      const handleEvent = (eventName, callback, manualEmit = false) => {
        return event => {
          event.stopPropagation()
          if (!manualEmit) {
            this.$emit(eventName, event)
          }
          if (callback) {
            callback(event)
          }
        }
      }
      return {
        ...this.$listeners,
        input: handleEvent(
          'input',
          event => {
            const newValue = event.target.value.trim()
            if (newValue !== this.value.toString().trim()) {
              this.$emit('input', newValue)
            }
          },
          true
        ),
        keypress: handleEvent('keypress'),
        keyup: handleEvent('keyup'),
        keydown: handleEvent('keydown'),
        focus: handleEvent('focus', () => {
          // eslint-disable-next-line vue/no-side-effects-in-computed-properties
          this.isFocused = true
        }),
        blur: handleEvent('blur', () => {
          // eslint-disable-next-line vue/no-side-effects-in-computed-properties
          this.isFocused = false
        }),
        change: handleEvent(
          'change',
          event => {
            this.$emit('change', event.target.value.trim())
          },
          true
        )
      }
    }
  }
}
</script>

<style lang="scss" module>
@import '~@branding';

.focused {
  border-color: var(--primary-color);
  outline: none;
}

.input {
  &::-webkit-input-placeholder {
    color: $app-text-color;
    opacity: config('opacity.75');
  }

  &:focus {
    outline: none;
  }

  &[disabled] {
    color: $app-disabled-text-color;
    cursor: not-allowed;
    opacity: config('opacity.50');
  }
}
</style>
