import branding from '@branding'

export const state = {
  gridRowNum: parseFloat(branding.gridRowNum),
  gridColNum: parseFloat(branding.gridColNum),
  gridHeight: String(branding.gridHeight),
  menuModalShown: false,
  addSourceModalShown: false,
  manageSourcesModalShown: false,
  settingsModalShown: false,
}

export const getters = {
  interfaceVariables(state) {
    return {
      "--grid-row-num": state.gridRowNum,
      "--grid-col-num": state.gridColNum,
      "--grid-height": state.gridHeight,
    }
  }
}

export const actions = {
  setMenuModalShown({ commit }, newValue) {
    commit('SET_MENU_MODAL_SHOWN', newValue)
  },
  setAddSourceModalShown({ commit }, newValue) {
    commit('SET_ADD_SOURCE_MODAL_SHOWN', newValue)
  },
  setManageSourcesModalShown({ commit }, newValue) {
    commit('SET_MANAGE_SOURCES_MODAL_SHOWN', newValue)
  },
  setSettingsModalShown({ commit }, newValue) {
    commit('SET_SETTINGS_MODAL_SHOWN', newValue)
  },
  setGridRowNum({ commit }, newValue) {
    commit('SET_GRID_ROW_NUM', newValue)
  },
  setGridColNum({ commit }, newValue) {
    commit('SET_GRID_COL_NUM', newValue)
  },
}

export const mutations = {
  SET_MENU_MODAL_SHOWN(state, newValue) {
    state.menuModalShown = newValue
  },
  SET_ADD_SOURCE_MODAL_SHOWN(state, newValue) {
    state.addSourceModalShown = newValue
  },
  SET_MANAGE_SOURCES_MODAL_SHOWN(state, newValue) {
    state.manageSourcesModalShown = newValue
  },
  SET_SETTINGS_MODAL_SHOWN(state, newValue) {
    state.settingsModalShown = newValue
  },
  SET_GRID_ROW_NUM(state, newValue) {
    state.gridRowNum = newValue
  },
  SET_GRID_COL_NUM(state, newValue) {
    state.gridColNum = newValue
  },
}
