<template>
  <button
    :class="[
      ['type-' + type],
      {
        ['button-active']: active,
        ['icon']: icon
      }
    ]"
    class="button"
    @click="onClick"
  >
    <div class="flex justify-start leading-none">
      <div
        v-if="icon"
        class="flex flex-col justify-center"
      >
        <BaseIcon
          :icon="icon"
          class="my-0 w-4 block text-center leading-none"
        />
      </div>
      <div class="w-full">
        <slot/>
      </div>
    </div>
  </button>
</template>

<script>
export default {
  props: {
    icon: {
      type: String,
      default: ''
    },
    active: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: '',
      validate(value) {
        return (
          [
            'main',
            'confirm',
            'success',
            'danger',
            'bordered'
          ].indexOf(value) !== -1
        )
      }
    }
  },
  methods: {
    onClick(event) {
      this.$emit('click', event)
    }
  }
}
</script>
