<template>
  <div id="app" class="relative">
    <AppGlobalModal />
    <AppPlayerList />
    <AppActionButton /> 
  </div>
</template>

<script>
import Vue from 'vue'
import AppGlobalModal from '@components/AppGlobalModal'
import AppPlayerList from '@components/AppPlayerList'
import AppActionButton from '@components/AppActionButton'

import { interfaceGetters } from '@store/helpers'

export default {
  name: 'App',
  components: {
    AppGlobalModal,
    AppPlayerList,
    AppActionButton
  },
  computed: {
    ...interfaceGetters
  },
  watch: {
    interfaceVariables: {
      handler() {
        document.body.setAttribute(
          'style',
          new Vue({
            render: h => h('div', { style: this.interfaceVariables })
          }).$mount().$el.style.cssText
        )
      },
      immediate: true
    }
  }
}
</script>